export default {
	data() {
		return {	
			collectionInfo: [],
			ppk: 0,
		}
	},
	methods: {
		async getCollectionInfo() {
			const res = await this.$myRequest({
				// url: "/collectionInfo/4535/",
				url: "/collectionInfo/"+this.ppk+"/",
			})
			// console.log(res)
			this.collectionInfo = res.data.data.items
		} 
	},
	onLoad(options) {
		// console.log(options)
		this.ppk = options.pk
		this.getCollectionInfo()
	}
}
